import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import SwitchCronJob from './ControlCronjob';
import AddCronJobKeyword from './AddCronJobKeywordDialog';
import DeleteIcon from '@mui/icons-material/Delete';
import Grid from '@mui/material/Grid';

export default function CronJobBar() {
  const [open, setOpen] = React.useState(false);
  const [chipData, setChipData] = React.useState([]);
  const [minuteremaining, setminuteremaining] = React.useState(0);
  const [nextcronjobtime,setnextcronjobtime] = React.useState(0);
  const statuses = ['pending','processing','completed']
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const deleteKeyword=(keyword, index)=>{
    const token=localStorage.getItem("token")
    var myHeaders = new Headers();
    myHeaders.append("Authorization", `Bearer ${token}`);
    myHeaders.append("Content-Type", "application/json");
    myHeaders.append('Access-Control-Allow-Origin', '*');

    var raw = JSON.stringify({
    "id": keyword['_id'],
    });

    var requestOptions = {
    method: 'DELETE',
    headers: myHeaders,
    body: raw,
    redirect: 'follow'
    };

    fetch(`http://${process.env.REACT_APP_API_KEY}/cronjob`, requestOptions)
    .then(response=>{
      setChipData((oldChipData) => {
        return oldChipData.filter((c, i) => i !== index);
        });
    })
    .catch(error => console.log('error', error));
  }
  const handleDelete = (chip, index) => {
    deleteKeyword(chip , index);
  };

      React.useEffect(() => {
            fetch(`http://${process.env.REACT_APP_API_KEY}/cronjob`, {
                method: "GET",
                headers: {
                    "Content-Type": "application/json",
                    "Authorization": `Bearer ${localStorage.getItem("token")}`,

                }
            })
                .then(res => res.json())
                .then(res => {
                  console.log(res,"---")
                //    data_rows.push(...res.data)
                if (res.data.length > 0) {
                  setChipData((prevrows) => ([...prevrows, ...res.data]))
                }
                    // setRows([...res.data])
                })
                .catch(err => console.log(err))

                fetch(`http://${process.env.REACT_APP_API_KEY}/cronjobtime`, {
                  method: "GET",
                  headers: {
                      "Content-Type": "application/json",
                      "Authorization": `Bearer ${localStorage.getItem("token")}`,
  
                  }
              })
                  .then(res => res.json())
                  .then(res => {
                    console.log(res,"---")
                  //    data_rows.push(...res.data)
                  if (res.data) {
                    setnextcronjobtime(res.data["next_cronjob_time"]);
                    setminuteremaining(Math.floor(res.data["minutes_remaining"]));
                  }
                      // setRows([...res.data])
                  })
                  .catch(err => console.log(err))
        
    }, []);
  return (
    <>
    <Grid container spacing={3} style={{paddingLeft:"20px"}}>
      <Grid item xs={3}>
        <Typography variant="h6" style={{color:"black"}} component="div">
          Control CronJob
        </Typography>
      </Grid>
      <Grid item>
        <SwitchCronJob/>
      </Grid>
    </Grid>
    <br/>
    <Grid container spacing={3} style={{paddingLeft:"20px"}}>
      <Grid item xs={3}>
          <Typography style={{marginLeft:"25px"}} variant="h7" style={{color:"black"}}>
                Next Cron Job time: {nextcronjobtime}
          </Typography>
      </Grid>
      <Grid item>
        <spacer type="horizontal" width="25px" height="100">  </spacer>
        <Typography style={{paddingLeft:"25px"}} variant="h7" style={{color:"black"}}>
            Minutes Remaining: {minuteremaining}
        </Typography>
      </Grid>
    </Grid>
    <br/>
    <Box sx={{ flexGrow: 1 }}>
      <AppBar position="static" style={{backgroundColor:"white"}}>
        <Toolbar>
          <Typography variant="h6" style={{color:"black"}} component="div" sx={{ flexGrow: 1 }}>
            CronJob Keywords
          </Typography>
          <Button color="primary" variant='contained' style={{marginLeft:"15px"}} onClick={handleClickOpen}>Add Keyword</Button>
          
        </Toolbar>
      </AppBar>
    </Box>
    <AddCronJobKeyword handleClose={handleClose} handleClickOpen={handleClickOpen} open={open} chipData={chipData} setChipData={setChipData} />
    <br/>

    
    {console.log(chipData,"---chipData")}
    {chipData.map((data, index) => { 
       return <Box sx={{ flexGrow: 1 }}>
          <AppBar position="static" style={{backgroundColor:"white",marginBottom:"10px"}}>
            <Toolbar>
              <Typography variant="h8" style={{color:"black"}} component="div" sx={{ flexGrow: 1 }}>
                {data["key_phrase"]}
              </Typography>
              
              <Button  variant='contained' style={{marginLeft:"15px"}} onClick={() => handleDelete(data, index)} startIcon={<DeleteIcon />}>
                Delete
              </Button>
              
            </Toolbar>
          </AppBar>
        </Box>
      // return <Chip key={index} style={{marginBottom:"10px",marginRight:'5px'}}label={data} onDelete={() => handleDelete(data, index)} /> 
    })
    }
    </>
  );
}