import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import * as React from 'react';

export default function AddCronJobKeyword(props) {
  // const value = props.value;
  // const setValue = props.setValue;
  const [value, setValue] = React.useState('');
  const chipData = props.chipData;
  const setChipData = props.setChipData;
  
  
  const handleChange = (event) => {
      setValue(event.target.value);
  };
  
  const insertKeyword = (keyword) => {
      const token=localStorage.getItem("token")
      var myHeaders = new Headers();
      myHeaders.append("Authorization", `Bearer ${token}`);
      myHeaders.append("Content-Type", "application/json");
      myHeaders.append('Access-Control-Allow-Origin', '*');

      var raw = JSON.stringify({
      "keyphrases": [keyword.toLowerCase()],
      });

      var requestOptions = {
      method: 'POST',
      headers: myHeaders,
      body: raw,
      redirect: 'follow'
      };

      fetch(`http://${process.env.REACT_APP_API_KEY}/cronjob`, requestOptions)
      .then(response=>{
          setChipData((oldChipData) => [...oldChipData, {'key_phrase': keyword}]);
          props.handleClose();
      })
      .catch(error => console.log('error', error));
  }
  const handleAddition = (event) => {
    if (value.length > 0) {
      insertKeyword(value);
    }
    else{
      alert("The length of Keyword should be greater than zero");
    }


  };
  


  return (
    <div>
      <Dialog open={props.open} onClose={props.handleClose}>
        {/* <DialogTitle>Keywords</DialogTitle> */}
        <DialogContent>
          <TextField
            autoFocus
            placeholder='Enter Keyword'
            margin="dense"
            id="name"
            label=""
            type="text"
            fullWidth
            variant="outlined"
            onChange={handleChange}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={props.handleClose}>Cancel</Button>
          <Button onClick={handleAddition}>Add</Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}