import * as React from 'react';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';

export default function SwitchCronJob() {
  const [checked, setChecked] = React.useState(false);

  const changecontrolcronjob = (controlcronjob) => {
    const token=localStorage.getItem("token")
    var myHeaders = new Headers();
    myHeaders.append("Authorization", `Bearer ${token}`);
    myHeaders.append("Content-Type", "application/json");
    myHeaders.append('Access-Control-Allow-Origin', '*');

    var raw = JSON.stringify({
    "status": controlcronjob,
    });

    var requestOptions = {
    method: 'PUT',
    headers: myHeaders,
    body: raw,
    redirect: 'follow'
    };

    fetch(`http://${process.env.REACT_APP_API_KEY}/controlcronjob`, requestOptions)
    .then(response=>{
      setChecked(controlcronjob);
    })
    .catch(error => console.log('error', error));
  };
  const handleChange = (event) => {
    changecontrolcronjob(event.target.checked);
   
  };
  React.useEffect(() => {
    fetch(`http://${process.env.REACT_APP_API_KEY}/controlcronjob`, {
        method: "GET",
        headers: {
            "Content-Type": "application/json",
            "Authorization": `Bearer ${localStorage.getItem("token")}`,

        }
    })
        .then(res => res.json())
        .then(res => {
          console.log(res,"---")
          setChecked(res.data.startcronjob)
        })
        .catch(err => console.log(err))

}, []);
  return (
    <FormGroup>
        <Stack direction="row" spacing={1} alignItems="center">
            <Typography style={{color:"black"}}>Off</Typography>
                <FormControlLabel control={<Switch defaultChecked 
                checked={checked}
                onChange={handleChange}
      />} style={{color:"black"}} label="" />
            <Typography style={{color:"black"}}>On</Typography>
        </Stack>
      
    </FormGroup>
  );
}