import ButtonAppBar from "../utility/AppBar.jsx";
import ScrappingCard from "../components/home/ScrappingCard.jsx";
import {useNavigate} from 'react-router-dom';
import { useEffect, useState} from 'react';
import ProgressShow from "../utility/ProgressShow.jsx";

export default function Home(){
  const [token,settoken]= useState(localStorage.getItem("token"))
  const [checktoken,setchecktoken]=useState(false);
  const navigate = useNavigate();
  const handleToken = (res) => {
    console.log(res,"-----here")
    if (res.status === 200) {
      setchecktoken(true)
    }
    else{
      return navigate("/");
    }
  }
  const check_token=async(tokenin)=>{
      var myHeaders = new Headers();
      myHeaders.append("Authorization", `Bearer ${tokenin}`);
      myHeaders.append("Content-Type", "application/json");
      myHeaders.append('Access-Control-Allow-Origin', '*');

      var requestOptions = {
      method: 'GET',
      headers: myHeaders,
      redirect: 'follow'
      };

      fetch(`http://${process.env.REACT_APP_API_KEY}/twitter`, requestOptions)
        .then( response => handleToken(response)
        )
      .catch(error => console.log('error', error));
      
  }
  useEffect(async() => {
    await check_token(token);
    console.log("---",checktoken)

 },[token]); // eslint-disable-line react-hooks/exhaustive-deps
 
 if (checktoken){
  return (
    <>
      <ButtonAppBar />
      <ScrappingCard />
  </>
  );
 }
 else{
  return (
    <ProgressShow/>

  );
 }
}