import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

export default function NotificationTable(props) {
    const [rows, setRows] = React.useState([]);
    const updateRows=async(status)=>{
        fetch(`http://${process.env.REACT_APP_API_KEY}/notification?status=${status}`, {
            method: "GET",
            headers: {
                "Content-Type": "application/json",
                "Authorization": `Bearer ${localStorage.getItem("token")}`,

            }
        })
            .then(res => res.json())
            .then(res => {
            //    data_rows.push(...res.data)
                setRows((prevrows) => ([...prevrows, ...res.data]))
                // setRows([...res.data])
            })
            .catch(err => console.log(err))
    }
    
    React.useEffect(async() => {
        for (let status of props.status) {
            await updateRows(status);
        }

    }, []);  // eslint-disable-line react-hooks/exhaustive-deps
    return (
        <>
            <TableContainer component={Paper}>
                <Table sx={{ minWidth: 650 }} aria-label="simple table">
                    <TableHead>
                        <TableRow>
                            <TableCell>Ref No</TableCell>
                            <TableCell align="right">Key Phrase</TableCell>
                            <TableCell align="right">Start Date</TableCell>
                            {(props.status[0] === "completed"  ? <TableCell align="right">Location</TableCell>:<></>) }
                            <TableCell align="right">Status</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>

                        {
                            rows.map((row,index2) => (
                                
                                <TableRow
                                    key={row._id}
                                //   sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                >
                                    <TableCell component="th" scope="row">
                                        {index2 + 1}
                                    </TableCell>
                                    <TableCell align="right">{row.key_phrase}</TableCell>
                                    <TableCell align="right">{row.start_date}</TableCell>
                                    {(props.status[0] === "completed"  ? <TableCell align="right">{row.location}</TableCell>:<></>) }
                                    <TableCell align="right">{row.status}</TableCell>
                                </TableRow>
                            ))
                        
                        }
                    </TableBody>
                </Table>
            </TableContainer>
        </>
    );
}