import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import NativeDatePicker from '../../utility/DatePickerComponent';
import MultipleChips from './MultipleChips';
import Grid from '@mui/material/Grid';
import Snackbar from '@mui/material/Snackbar';
import MuiAlert from '@mui/material/Alert';
import { useNavigate } from 'react-router-dom';

const Alert = React.forwardRef(function Alert(props, ref) {
    return <MuiAlert elevation={6} ref={ref} variant="filled" {...props} />;
  });

export default function ScrappingCard() {
  const [value, setValue] = React.useState('');
  const [chipData, setChipData] = React.useState([]);
  const [startDate, setstartDate] = React.useState("2017-05-24");
  const [endDate, setendDate] = React.useState("2017-05-24");
  const [open, setOpen] = React.useState(false);
  const navigate = useNavigate();

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpen(false);
  };

  const showModal =(res)=>{
    console.log(res.status);
    if (res.status===200){
      setOpen(true);

  }
  else{
    return navigate("");
  }
    
    }
  const startScrapping=()=>{
    const token=localStorage.getItem("token")
    var myHeaders = new Headers();
    myHeaders.append("Authorization", `Bearer ${token}`);
    myHeaders.append("Content-Type", "application/json");
    myHeaders.append('Access-Control-Allow-Origin', '*');

    var raw = JSON.stringify({
    "key_phrases": chipData,
    "start_date": startDate,
    "end_date": endDate,
    "method":"scraped",
    "break":false
    });

    var requestOptions = {
    method: 'POST',
    headers: myHeaders,
    body: raw,
    redirect: 'follow'
    };

    fetch(`http://${process.env.REACT_APP_API_KEY}/twitter`, requestOptions)
    .then(response => showModal(response))
    .catch(error => console.log('error', error));
  }

  const getTimeStamp = (date) => {
    var myDate = date
    myDate = myDate.split("-");
    var newDate = new Date( myDate[0], myDate[1] - 1, myDate[2]);
    return (newDate.getTime());
    }

  const handleSubmit = () => {
      
      if (chipData.length <= 0) {
        return alert("Please enter atleast one keyword");
      }
      else if (getTimeStamp(startDate) >= getTimeStamp(endDate)) {
        return alert("Start date should be less than end date");
        }
      else{
          startScrapping();
      }
  };
  return (
      <>
      <Snackbar open={open} autoHideDuration={6000} onClose={handleClose}>
        <Alert onClose={handleClose} severity="success" sx={{ width: '100%' }}>
          Crawling has been started!
        </Alert>
      </Snackbar>
    <Card sx={{ minWidth: 275 }}>
      <CardContent>
        <MultipleChips value={value} setValue={setValue} chipData={chipData} setChipData={setChipData} />
        <br/>
        <Grid container>
                <Grid item xs={12}>
                    <Grid container justifyContent="center" alignItems="center" spacing={1} >
                        <Grid item xs={6}>
                            <Grid container justifyContent="flex-end" >
                                <Grid item xs={8} md={6} lg={5}>
                                    <Grid container justifyContent="flex-start" >
                                        <Typography> Enter Start Date</Typography>
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>
                        <Grid item xs={6}>
                            <Grid container justifyContent="flex-start" >
                                <NativeDatePicker label="Start Date" Date={startDate} setDate={setstartDate} />
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            
        </Grid>
        <br/>
        <Grid container>
                <Grid item xs={12}>
                    <Grid container justifyContent="center" alignItems="center" spacing={1} >
                        <Grid item xs={6}>
                            <Grid container justifyContent="flex-end" >
                                <Grid item xs={8} md={6} lg={5}>
                                    <Grid container justifyContent="flex-start" >
                                        <Typography> Enter End Date</Typography>
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>
                        <Grid item xs={6}>
                            <Grid container justifyContent="flex-start" >
                                <NativeDatePicker label="End Date" Date={endDate} setDate={setendDate}/>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
            
        </Grid>
        <br/>

      </CardContent>
      <CardActions style={{display:"flex",justifyContent:"center"}}>
        <Button size="medium" variant="outlined" style={{marginBottom:"20px"}} onClick={handleSubmit}>Start Scrapping</Button>
      </CardActions>
    </Card>
    </>
  );
}