import Chip from '@mui/material/Chip';
import * as React from 'react';
import TextField from '@mui/material/TextField';
import { Typography } from '@mui/material';
import Grid from '@mui/material/Grid';

export default function MultipleChips(props) {
    const value = props.value;
    const setValue = props.setValue;
    const chipData = props.chipData;
    const setChipData = props.setChipData;
    
    
    const handleChange = (event) => {
        setValue(event.target.value);
    };
    
    const handleAddition = (event) => {
        if (event.key === 'Enter') {
            let value_lower = value.toLowerCase()
            setChipData((oldChipData) => [...oldChipData, value_lower]);
        }
    };
    
    const handleDelete = (chip, index) => {
        setChipData((oldChipData) => {
        return oldChipData.filter((c, i) => i !== index);
        });
    };
    
    return (
        <div>
            <br />
            <Grid container spacing={3} >
                <Grid item xs={12}>
                    <Grid container justifyContent="center" >
                        <Grid item xs={7}>
                            <Grid container justifyContent="center" >
                        {
                            chipData.map((data, index) => { return <Chip key={index} style={{marginBottom:"10px",marginRight:'5px'}}label={data} onDelete={() => handleDelete(data, index)} /> })
                        }
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
                <br />
                <br />
                <Grid item xs={12}>
                    <Grid container justifyContent="center" alignItems="center"  spacing={1}>
                        <Grid item xs={6}>
                            <Grid container justifyContent="flex-end" >
                                <Grid item xs={8} md={6} lg={5}>
                                    <Grid container justifyContent="flex-start" >
                                        <Typography> Enter Keywords for Scrapping</Typography>
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>
                        <Grid item xs={6}>
                            <Grid container justifyContent="flex-start" >
                                <TextField
                                    id="outlined-basic"
                                    placeholder='Type Keyword and press enter'
                                    label=""
                                    variant="outlined"
                                    value={value}
                                    onKeyDown={handleAddition}
                                    onChange={handleChange}
                                />
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
        </Grid>
        </div>
    );
}