import React from 'react';
import {useNavigate} from 'react-router-dom';
import styles from "./LoginCard.module.css";

import IconButton from '@mui/material/IconButton';

import OutlinedInput from '@mui/material/OutlinedInput';
import InputAdornment from '@mui/material/InputAdornment';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';
import { useEffect } from 'react';



  
 

function LoginCard(){
    const [name,changename]=React.useState('');
    const [password,changepassword]=React.useState('');
    const [showPassword,changeshowPassword]=React.useState(false);
    const navigate = useNavigate();

    const handleToken = (res) => {
        if (res.status === 200) {
            return navigate("/home")
        }
        else{
            return navigate("");
        }
    }
    const check_token=(token)=>{
        var myHeaders = new Headers();
        myHeaders.append("Authorization", `Bearer ${token}`);
        myHeaders.append("Content-Type", "application/json");
        myHeaders.append('Access-Control-Allow-Origin', '*');

        var requestOptions = {
        method: 'GET',
        headers: myHeaders,
        redirect: 'follow'
        };

        fetch(`http://${process.env.REACT_APP_API_KEY}/twitter`, requestOptions)
        .then(response => handleToken(response))
        .catch(error => console.log('error', error));
    }

    useEffect(() => {
        const token=localStorage.getItem("token")
        console.log(token)
        check_token(token)
     },[]); // eslint-disable-line react-hooks/exhaustive-deps
    const handle_logging=async(res)=>{
        if (res.status===200){
            let data = await res.json();
            localStorage.setItem("token",data["access_token"]);
            navigate("/home");

        }
        else{
            alert("Invalid Credentials")
        }
    }
    const checkuser=()=>{
        
        console.log(name,password)
        const logging_in=()=>{

            var myHeaders = new Headers();

            myHeaders.append("Content-Type", "application/json");
            myHeaders.append('Access-Control-Allow-Origin', '*');
            var raw = JSON.stringify({
            "username": name,
            "password": password
            });

            var requestOptions = {
            method: 'POST',
            headers: myHeaders,
            body: raw,
            redirect: 'follow'
            };

            fetch(`http://${process.env.REACT_APP_API_KEY}/login`, requestOptions)
            .then(response => handle_logging(response))
            .catch(error => console.log('error', error));
        }

    logging_in()

    }


   const handleChange =(prop)=>(event)=> {
       console.log(event.target.name,event.target.value)
       if(prop==="name"){
            changename(event.target.value)
       }
       else if(prop==="password"){
        changepassword(event.target.value)
       }
        }
    
    const  handleClickShowPassword = () => {
        changeshowPassword(!showPassword)

      };
    
     const handleMouseDownPassword = (event) => {
        event.preventDefault();
        
      };

     const handleSubmit = (event) => {
        event.preventDefault();
        if(name===""){
            return alert("username is required");
        }
        else if(password===""){
            return alert("password is required");
        }
        else{
            checkuser()
        }
        
      }
      const inputStyle = { WebkitBoxShadow: "0 0 0 1000px white inset" };
        return(
        
            <div className={styles.containerlogincard}>
            <div className={styles.containerlogincardin}>

            
            <form  onSubmit={handleSubmit} noValidate>
            
                <h5> Log in to your account</h5>
                <div className={styles.email11}>
                    <div className={styles.email1}>
                    <OutlinedInput
                            fullWidth style={{marginTop:"2%",height:"40px"}}  
                            inputProps={{ style: inputStyle }}
                            variant="outlined"
                            placeholder="Enter Username"
                            id="name" name="name"  value={name} onChange={handleChange('name')}
                        />

                    </div>
                    {/* <div className={styles.email} style={{display:emailvalidate?"none":"flex"}}>  
                        <p> Account is not registered</p>
                    </div> */}

                    <div className={styles.email1}>


                        <OutlinedInput
                            fullWidth style={{marginTop:"5%",height:"40px"}} 
                            inputProps={{ style: inputStyle }} 
                            size="small"
                            variant="outlined"
                            id="password"
                            name="password"
                            placeholder="Password"
                            type={showPassword ? 'text' : 'password'}
                            value={password}
                            onChange={handleChange('password')}
                            endAdornment={
                            <InputAdornment position="end">
                                <IconButton
                                aria-label="toggle password visibility"
                                onClick={handleClickShowPassword}
                                onMouseDown={handleMouseDownPassword}
                                edge="end"
                                >
                                {showPassword ? <Visibility /> : <VisibilityOff />}
                                </IconButton>
                            </InputAdornment>
                            }
                        />
                    
                        
                    
                    </div>   
                        {/* <div className={styles.email} style={{display:passwordvalidate?"none":"flex"}}>
                            <p> Password is not valid</p>
                        </div> */}
                </div>
                <div className={styles.loginb} >
                    
                <button className={styles.login}  type="submit"> Log In</button>
                
                </div>
                
                </form>
            </div>
        </div>
    )
}
export default LoginCard;